<template>
  <div ref="wrapper">
    <slot></slot>
  </div>
</template>

<script>
  import BScroll from 'better-scroll'

  export default {
    props: {
      // 
      probeType: {
        type: Number,
        default: 1
      },
      click: {
        type: Boolean,
        default: true
      },
      // 
      listenScroll: {
        type: Boolean,
        default: false
      },
      // 
      pullup: {
        type: Boolean,
        default: false
      },
      // 
      beforeScroll: {
        type: Boolean,
        default: false
      },
      refreshDelay: {
        type: Number,
        default: 20
      },
      // data
      data: {
        tpye: Array,
        default: null
      }
    },
    // el,  this.$el  document  Vue.nextTick/vm.$nextTick
    mounted() {
      setTimeout(() => {
        this._initScroll()
      }, 20)
    },

    methods: {
      _initScroll() {
        if (!this.$refs.wrapper) {
          return
        }
        this.scroll = new BScroll(this.$refs.wrapper, {
          probeType: this.probeType,
          click: this.click
        })
        // console.log('mounted,data', this.$refs.wrapper, this.$refs.wrapper.firstChild.clientHeight)

        if (this.listenScroll) {
          // meVue
          let me = this
          this.scroll.on('scroll', (pos) => {
            // scroll,pos
            me.$emit('scroll', pos)
          })
        }

        // 
        if (this.pullup) {
          // 
          this.scroll.on('scrollEnd', () => {
            // scroll50px
            if (this.scroll.y <= (this.scroll.maxScrollY + 50)) {
              // 
              this.$emit('scrollToEnd')
            }
          })
        }

        if (this.beforeScroll) {
          // 
          this.scroll.on('beforeScrollStart', () => {
            this.$emit('beforeScroll')
          })
        }
      },
      disable() {
        this.scroll && this.scroll.disable()
      },
      enable() {
        this.scroll && this.scroll.enable()
      },
      refresh() {
        this.scroll && this.scroll.refresh()
      },
      scrollTo() {
        this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
      },
      scrollToElement() {
        this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
      }
    },

    // 
    watch: {
      data() {
        setTimeout(() => {
          this.refresh()
        }, this.refreshDelay)
        // console.log('watch', this.$refs.wrapper.firstChild.clientHeight)
      }
    }
  }
</script>

<style scoped lang="stylus">

</style>


